import {ListGroup, ListGroupItem} from "react-bootstrap";


function Fpls(props) {

    const fpls_items = props.fpls.map((fpl) => (
        <ListGroupItem
            active={props.selection.includes(fpl.id)}
            key={fpl.id}
            onClick={() => props.onClick(fpl.id)}>
            <p>{fpl.d_airport} &rarr; {fpl.a_airport} {props.updated.includes(fpl.id) ? '*' : ''}</p>
            <small>{fpl.id} -- {fpl.aircraft_type}</small>
        </ListGroupItem>));


    return (
        <div className="wrapper">
            <ListGroup style={{
                cursor: "pointer",
                width: '100%',
                height:'60vh',
                overflowY: 'scroll'
            }}>
                {fpls_items}
            </ListGroup>
        </div>
    );
};

export default Fpls;