import Col from "react-bootstrap/Col";
import Fpls from "../fpls/Fpls";
import Map from "../map/Map";
import Row from "react-bootstrap/Row";
import React, {useState} from "react";

function FplsMap(props) {
    const [selection, setSelection] = useState([]);
    const [updated, setUpdated] = useState([]);

    const updateUpdatedFplId = (fpl_id, is_updated) => {
        const newUpdated = [...updated ];
        if(newUpdated.includes(fpl_id) && !is_updated){
            newUpdated.splice(newUpdated.indexOf(fpl_id),1);
        }else if (!newUpdated.includes(fpl_id)){
            newUpdated.push(fpl_id);
        }
        setUpdated(newUpdated);
    };

    const update_fpl = (fpl, beacon_to_edit, new_level) =>{
        updateUpdatedFplId(fpl.id, false);
        let new_fpl = {...fpl};
        const new_beacon = {...beacon_to_edit, level:parseInt(new_level)};
        new_fpl.beacons = fpl.beacons.map(beacon=>
                beacon.name === beacon_to_edit.name ?
                   new_beacon : beacon );
        props.onChangedFpl(new_fpl);
    };

    const toggleSelection = (fpl_id) => {
        const newSelection = selection.includes(fpl_id) ?
                selection.filter((i) => i !== fpl_id) :
                [...selection, fpl_id];
        setSelection(newSelection);
    };

    return (
        <Row>
            <Col>
                <h4>Plans de vol</h4>
                <Fpls fpls={props.fpls} onClick={toggleSelection} selection={selection} updated={updated}/>
            </Col>
            <Col xs={9}>
                <h4>Carte</h4>
                <Map airports={props.airports} beacons={props.beacons} selection={selection} fpls={props.fpls} onSubmitFpl={update_fpl} onUpdateFpl={updateUpdatedFplId}/>
            </Col>
        </Row>

    )
}
export default FplsMap;

